@import './mixins';
@import './colors';

#home {
    @include horizontal-layout(center, center);
    background-color: $primary-2;
	padding-top: 59.6px;
	
	@include sm {
		padding-top: 69.6px;;
	}

	@include md {
		padding-top: 85.6px;
	}
    
    .inner {
        @include inner;
        
        @include md {
            @include horizontal-float-right(stretch, center);
        }

        img {
            max-width: 100%;
            height: auto;

            @include md {
                width: calc(5/12*100%);
                float: right;
            }
        }

        .content {
            @include vertical-layout(flex-start, center);
            padding: 50px 0;

            @include md {
                width: calc(7/12*100%);
                float: right;
            }

            p {
                color: $text-2;

                &#occupation {
                    margin: 20px 0;
                    padding: 10px 20px 10px 0px;
                    border-top: 1px solid $text-2l;
                    border-bottom: 1px solid $text-2l;
                }
            }

            #portfolio-ln {
                font-size: 15px;
                font-weight: 600;;
                margin-top: 10px;
                padding: 5px 10px;
                border: 2px solid $text-1;
                border-radius: 20px;

                svg {
                    margin-left: 10px;
                }

                &:hover {
                    color: $text-3;
                    background-color: $primary-3;

                    svg {
                        path {
                            fill: $text-3;
                        }
                    }
                }
            }
        }
    }
}

